.user_panel__header {
  height: 60px;
  margin-bottom: 0;
  box-shadow: 0 3px 5px rgba(100, 100, 100, 0.49);
}

ul.user_panel__nav {
  height: 100%;
  width: 100%;
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: row;

  div.user_panel_nav__logo {
    height: 50%;
    width: 200px;
    font-size: 0;
    background-repeat: no-repeat;
    background-size: contain;
    background-image: url("../../assets/Your_budget_logo.svg");
    cursor: pointer;
  }

  li {
    height: 100%;
    padding: 15px;
    display: flex;
    align-items: center;
    justify-content: center;
    list-style: none;
    text-align: center;
    cursor: pointer;
    box-sizing: border-box;
    transition: 0.3s ease-in-out;

    &:hover {
     color: white;
    }
  }
}

main {
  background-color: rgb(242, 239, 235);
}
//min-height: 80px;
.user_panel {
  width: 100%;
  padding: 5% 0 5% 0;
  display: flex;
  align-items: center;
  justify-content: center;
  background-color: rgb(243, 240, 236);
}


.main_panel {
  height: 80vh;
  background-color: white;
  border: 1px solid lightgrey;
  box-shadow: 8px 10px 20px 0 rgba(46,61,73,.15);
}
